import React, {useState} from "react";
import { Modal } from "@mui/material";
import { Box } from "@mui/material";
import { Text, Button, Input} from "components";
import { useNavigate } from "react-router-dom";
import { SET_PROJECT_BASIC_DETAIL } from "store/slices/basicInformation.slice";
import { useDispatch } from "react-redux";

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '35%',
  height: '63%',
  bgcolor: 'blur',
  boxShadow: 24,
};
const CreateProjectModal = (props) => {
  const [activeInputValue, setActiveInputValue] = useState("");
  const [projectDetal,setProjectDetail] = useState({});
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const updateInputValue = (name) => {
    setActiveInputValue(name)
  }
  const onChangeInputHandler = (e) => {
     setProjectDetail( curDetail => {
           return {...curDetail,[e.target.name] : e.target.value}
     })
  }
  const createProject = () => {
    console.log(projectDetal);
    dispatch(SET_PROJECT_BASIC_DETAIL(projectDetal));
    navigate(`/${props?.navigationKey}`)
  }
  return (
    <>
      <Modal
         open={props?.createProjectModalToShow}
         aria-labelledby="modal-modal-title"
         aria-describedby="modal-modal-description"
      >
        <Box sx={style}> 
        <div className="max-h-[97vh] sm:w-[100%] md:w-[100%]">
          <div className="bg-gray_901 flex items-center justify-start p-[30px] md:px-[20px] rounded-[4px] w-[100%]">
            <div className="flex flex-col items-start justify-start mb-[8px] mt-[20px] w-[100%]">
              <Text
                className="font-semibold text-gray_50 text-left w-[auto]"
                as="h3"
                variant="h3"
              >
                Start Your project
              </Text>
              <div className="flex md:flex-1 flex-col gap-[20px] items-center justify-start md:w-[100%] w-[100%] mt-[24px]">
                <div className="flex flex-col gap-[20px] items-center justify-start w-[100%]">
                <div className="flex flex-col gap-[8px] items-start justify-start self-stretch sm:w-[100%] w-[auto] relative">
                    <Text
                      className="font-semibold text-gray_300 text-left w-[auto] after:content-['*'] after:ml-0.5 after:text-red-500"
                      variant="body2"
                    >
                      Project Name
                    </Text>
                    <Input
                      wrapClassName= {`common-pointer bg-gray_902 border-[1px] border-solid p-[12px] rounded-[4px] sm:w-[100%] w-[100%]`}
                      className="font-normal not-italic p-[0] placeholder:text-gray_801 border-[1px] text-[12px] text-gray_300 text-left sm:w-[100%] w-[512px] focus:border-gray_501"
                      name="ProjectName"
                      placeholder="Enter your Project Name"
                      onChangeHandler={onChangeInputHandler}
                      activeInputValue = {activeInputValue}
                      updateInputValue = {updateInputValue}
                    ></Input>
                  </div>
                  <div className="flex flex-col gap-[8px] items-start justify-start self-stretch sm:w-[100%] w-[auto] relative">
                    <Text
                      className="font-semibold text-gray_300 text-left w-[auto]"
                      variant="body2"
                    >
                      <label htmlFor="Description">Description</label>
                    </Text>
                    <textarea
                      id="Description"
                      rows="4"
                      className={`block p-2.5 w-full text-sm text-gray_300 bg-gray_902 rounded-lg border-[1px] solid focus:border-gray_501 !font-inter !font-light p-[0] placeholder:text-gray_801 !text-[12px]`}
                      placeholder="Enter description of this model..."
                      name="Description"
                      onChange={onChangeInputHandler}
                    />
                  </div>
                 
                </div>
              </div>
              <div className="flex flex-row gap-[16px] items-center justify-center md:ml-[0] mt-[40px] md:w-[100%] w-[88%]">
                <Button 
                 onClick = {() => props?.updateCreateProjectMoalToshow(false)}
                 className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium sm:px-[20px] md:px-[40px] px-[64px] py-[10px] rounded-[4px] text-[14px] text-center text-teal_A400 w-[161px]">
                  Cancel
                </Button>
                <Button 
                  onClick={projectDetal.ProjectName ? createProject : null}
                  className= {`${projectDetal.ProjectName ? "bg-gradient" : "bg-teal_900"}  cursor-pointer font-medium px-[12px] py-[10px] rounded-[4px] text-[14px] text-center text-gray_900 w-[161px]`}>
                  Create
                </Button>
              </div>
            </div>
          </div>
        </div>
        </Box>
      </Modal>
    </>
  );
};

export default CreateProjectModal;
