import React from "react";
import { Modal } from "@mui/material";
import { Text, Img, Button } from "components";
import { Box } from "@mui/material";
import { useNavigate } from "react-router-dom";

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '33%',
  height: '50%',
  bgcolor: 'blur',
  boxShadow: 24,
};
const StartProjectModal = (props) => {
  const navigate = useNavigate("");
  const projectTypes = [
    {
      path: "frontend-select-model",
      label: "Front End Only",
      typeName:"Front End",
    },
    {
      path: "backend-select-model",
      label: "Back End Only",
      typeName: "Back End"
    },
    {
      path: "both-select-model",
      label: "Both Front End and Back End",
      typeName: "Both"
    },
  ];
  const cancelCreateProjectevent = () => {
    props?.updateNavigationKey("");
    props?.updateStartProjectModalToShow(false);
  }
  const onClickProceedHandler = () => {
    props?.updateStartProjectModalToShow(false);
    props?.updateCreateProjectMoalToshow(true);  
    // navigate(`/${props?.navigationKey}`)
  }
  return (
    <>
      <Modal
        open={true}
        // onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
        <div className="max-h-[97vh] overflow-y-auto sm:w-[100%] md:w-[100%]">
          <div className="bg-gray_901 flex items-center justify-start p-[48px] md:px-[20px] rounded-[4px] w-[100%]">
            <div className="flex flex-col items-start justify-start mb-[17px] mt-[20px] w-[100%]">
              <Text
                className="font-semibold text-gray_50 text-left w-[auto]"
                as="h3"
                variant="h3"
              >
                What do you want to build today?
              </Text>
              {projectTypes.map((item,idx) => {
                return(
                  <div 
                   onClick={() => props?.startProjectHandler(item)}
                   className="flex flex-row gap-[8px] items-start justify-start mt-[25px] md:w-[100%] w-[72%]"
                   key={idx}
                   >
                  {props?.navigationKey === item.path ? (
                     <Img
                     src="images/img_contrast.svg"
                     className="h-[20px] w-[20px] cursor-pointer"
                     alt="settings_Two"
                   /> 
                  ) : (
                    <Img
                    src="images/img_settings_gray_300_20x20.svg"
                    className="h-[20px] w-[20px] cursor-pointer"
                    alt="settings"
                  />
                  )}
                  <Text
                    className="font-normal not-italic text-gray_300 text-left w-[auto] cursor-pointer"
                    as="h5"
                    variant="h5"
                  >
                    {item.label}
                  </Text>
                </div>
                )
              })}
              <div className="flex flex-row gap-[16px] items-center justify-start md:ml-[0] ml-[25px] mt-[74px] md:w-[100%] w-[88%]">
                <Button 
                 onClick={cancelCreateProjectevent}
                 className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium sm:px-[20px] md:px-[40px] px-[64px] py-[10px] rounded-[4px] text-[14px] text-center text-teal_A400 w-[161px]">
                  Cancel
                </Button>
                <Button 
                  onClick={props?.navigationKey ? onClickProceedHandler : null}
                  className={`${props?.navigationKey ?"bg-gradient" : "bg-teal_900"} cursor-pointer font-medium px-[12px] py-[10px] rounded-[4px] text-[14px] text-center text-gray_900 w-[161px]`}>
                  Proceed
                </Button>
              </div>
            </div>
          </div>
        </div>
        </Box>       
      </Modal>
    </>
  );
};

export default StartProjectModal;
